.header-color{
  background: #8A2387;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, left top, right top, from(#F27121), color-stop(#E94057), to(#8A2387));
background: -webkit-linear-gradient(left, #F27121, #E94057, #8A2387);
background: linear-gradient(to right, #F27121, #E94057, #8A2387); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.landing-grid{
  background: #8A2387;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#F27121), color-stop(#E94057), to(#8A2387));
  background: -webkit-linear-gradient(left, #F27121, #E94057, #8A2387);
  background: linear-gradient(to right, #F27121, #E94057, #8A2387); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 text-align: center;
 position: fixed;
 top: 0%;
 left: 0%;
 width: 100%;
 height: 100%;
}
.avatar-img{
  height: 250px;
  padding-top: 5em;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width:75%;
  margin:auto;
  border-radius: 10px; 

}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color:white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width:50%;
  margin:auto;
}
.banner-text p{
  color : white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 58%;
  margin: auto;

}
.social-links i{
  color:white;
  font-size: 5em;padding: 8px;

}
.projects-grid{
  display: flex;
  
}
.contact-body{
  margin: auto;
  background: grey;
  position: fixed;
  width: 100%;
  height: 100%;
justify-content: center;
  padding-top: 2em;
}
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  border: 2px solid red;
  margin-bottom: 20px;

}
.contact-grid h2{
  font-family: 'Anton',sans-serif
}
.contact-grid p{
  font-family: 'Oxygen',sans-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto; 
}
.list-style{
  font-size: 28px;
  font-family: Anton;
}
.contact-list i{
  font-size: 60px;
  padding-right: 5rem;

}

.resume-right-col{
  background: #27221f;
  color: white;
  padding: 2em;
}
